import axios from 'axios';
import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import '../style/login.css';
function Login(props) {
  const [form, setForm] = useState({
    username:'',
    password:'',
  })
  const navigate = useNavigate();
  const [error, setError] = useState('');  
  const handleChange = (e)=>{
    const {name, value} = e.target;
    setForm((prev)=>({
      ...prev,
      [name]:value
    } ))
  }

  const handleSubmit = async (e)=>{
    e.preventDefault();

    try{const res = await axios.post('http://localhost:9070/login',form);
      localStorage.setItem('token', res.data.token);
      localStorage.setItem('username', form.username);
      
      alert(' ');
      navigate('/ginipet');
    }
    catch(err){setError(' :    ');}


  }


  return (
    <main>
     <form action="" method='post' onSubmit={handleSubmit} className='login_form'>
     <h2></h2> 
      <p>
        <label htmlFor="username"></label>
        <input type="text" id="username"  required placeholder=' ' value={form.username}  name='username' onChange={handleChange}/>
      </p>

      <p>
        <label htmlFor="password"></label>
        <input type="password" id="password" required  name="password" placeholder=' '  value={form.password} onChange={handleChange} />
      </p>
      <p>
        <button type="submit" className='login_btn'></button>
      </p>
      {error && <p style={{color:'red', margin:'20px 0px'}}>{error}</p> }
      <p className='login_btns'>
      <Link to='/'> </Link>
      <Link to='/'> </Link>
      <Link to='/'></Link>
    </p>
     </form>
  
    </main>
  );
}

export default Login;