import React from 'react';
import { Link } from 'react-router-dom';
import '../style/gnb.css'
function Gnb(props) {
  return (
    <main>
     <ul className='gnb'>
     <li><Link to='/shop'> </Link></li>
        <li><Link to='/brand'> </Link></li>
        <li><Link to='/info'> </Link></li>
        <li><Link to='/event'></Link></li>
        <li><Link to='/customer'></Link></li>
      </ul> 
    </main>
  );
}

export default Gnb;