import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
function Nav(props) {
  const navigate =useNavigate();
  const username = localStorage.getItem('username'); //   
  const handleLogout = ()=>{
    localStorage.removeItem('token');
    localStorage.removeItem('username');
    navigate('/login');
    window.location.reload();// 
  }
  return (
    <>
     <nav>
      <ul className='user_menu'>
      {username? (
            <span className='login_id' style={{color:'white', marginTop:'-5px'}}>
              <b>{username}</b> .
              <button onClick={handleLogout} style={{marginLeft:'10px', border:'none', background:'#333', color:'white', fontSize:'16px'}}></button>
            </span>) : (<Link to='/login' style={{color:'white', textDecoration: 'none'}}></Link>
          )}
        <li><Link to='/join'></Link></li>
        <li><Link to='/order'></Link></li>
        <li><Link to='/cart'></Link></li>
      </ul>
      </nav> 
    </>
  );
}

export default Nav;