import React, {useState} from 'react';
import { useNavigate } from 'react-router-dom';
import UserNav from '../components/UserNav';
import Gnb from '../components/Gnb';
import '../style/header.css';
import {Link} from 'react-router-dom';
function Header(props) {
  const username = localStorage.getItem('username'); //    
  const [open, setOpen]= useState(false);
  const navigate =useNavigate();
  const handleLogout = ()=>{
    localStorage.removeItem('token');
    localStorage.removeItem('username');
    navigate('/login');
    window.location.reload();// 
  }
  return (
    <header>
      <div className='header_wrap'>
      <Link to='/ginipet'>
      <img src={`${process.env.PUBLIC_URL}/images/logo_color.png`} alt="" id='logo_color' />
      </Link>
        <UserNav />
        <Gnb /> 
      </div>
      <img src={`${process.env.PUBLIC_URL}/images/toggle.png`} 
      alt="" style={{zIndex:'100'}} id='toggle' onClick={()=>setOpen(true)}/>
      <img src={`${process.env.PUBLIC_URL}/images/cart.png`} alt="" id="cart" />

      <nav className='mobile_gnb' style={{left:open? '0%': '-100%'}}>
        <img src={`${process.env.PUBLIC_URL}/images/btn_close.png`} alt="" id="close" onClick={()=>setOpen(false)} />
        <ul className='mobile_gnb_menu'>
          <li><Link to='/shop' className='mobile_gnb_shop'> </Link></li>
          <li><Link to='/brand'  className='mobile_gnb_brand'> </Link></li>
          <li><Link to='/info'  className='mobile_gnb_info'> </Link></li>
          <li><Link to='/event'  className='mobile_gnb_event'></Link></li>
          <li><Link to='/customer' className='mobile_gnb_customer'></Link></li>
        </ul>

        <div>
          <ul className='mobile_user_menu'>
                  {username? (
                        <span className='login_id' style={{color:'white', marginTop:'-5px'}}>
                          <b>{username}</b> .
                          <button onClick={handleLogout} style={{marginLeft:'10px', border:'none', background:'#333', color:'white', fontSize:'16px'}}></button>
                        </span>) : (<Link to='/login' style={{color:'white', textDecoration: 'none'}}></Link>
                      )}
            <li></li>
            <li></li>
            <li></li>
          </ul>
         
        </div>
      </nav>
    </header>
  );
}

export default Header;